<template>
	<div class="app-container" v-loading="isLoad" >
		<el-form :label-position="labelPosition" label-width="80px" :model="form" ref='form'style="width:500px ;margin:0 auto;" :rules="rules">
			<el-form-item label="公司名称:">
				<el-input v-model="form.company" placeholder="请输入公司名称" disabled></el-input>
			</el-form-item>
			<el-form-item label="公司地址:" prop="address">
				<el-input v-model="form.address" placeholder="请输入公司地址"></el-input>
			</el-form-item>
			<el-form-item label="联系人:" prop="contacts">
				<el-input v-model="form.contacts" placeholder="请输入联系人"></el-input>
			</el-form-item>
			<el-form-item label="联系号码:" prop="contactsPhone">
				<el-input v-model="form.contactsPhone" placeholder="请输入联系号码"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button @click="saveAction">保存</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>


<script>
    /* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */

import {
     getManufacturerInfoByLogin,
	updateManufacturerInfo
} from 'api/admin/basicData';
import { backCN } from 'utils/index'
import{validatePhone,validateCall}from 'utils/validate'

	export default {
	data() {
		var checkPhone = (rule, value, callback) => {
			if (value === '') {
				callback(new Error('联系号码不能为空！'))

			} else {
				if (validatePhone(value) || validateCall(value)) {
					callback()
				} else {
					callback(new Error('请填写正确格式的手机号或座机号！'))
				}
			}
		}
		return {
			labelPosition: 'left',
			itemList: [],
			form: {
				id: '',
				company: '',
				address: '',
				contacts: '',
				contactsPhone: ''
			},
			rules: {
				address: [{ required: true, message: '地址不能为空' }],
				contacts: [{ required: true, message: '联系人不能为空' }],
				contactsPhone: [{ validator: checkPhone, trigger: 'blur' }]
			},
			isLoad: true
		}
	},
	watch: {},
	mounted() {
		this.ManufacturerInfoByLogin();
	},
	methods: {
		saveAction() {
			this.$refs['form'].validate((valid) => {
				if (valid) {
					let address=this.form.address;
					let contactsName=this.form.contacts;
					let contactsPhone=this.form.contactsPhone;
					this.isLoad=true;
						updateManufacturerInfo({address,contactsName,contactsPhone}).then(res=>{
							this.isLoad=false
							res.msg===0?this.$message.success("保存成功！"):this.$message.error(backCN(res.code))
						})
					

				} else {
					console.log('error submit!!');
					return false;
				}
			});
		},
		ManufacturerInfoByLogin() {
					this.isLoad = true
						getManufacturerInfoByLogin()
							.then(res => {
                                this.isLoad=false
								if (res.msg == 0) {
									this.form.company = res.name;
									this.form.address = res.address;
									this.form.contacts = res.contacts;
									this.form.contactsPhone = res.contactsPhone;
								}
							});
					
				
		}
	}
	};
</script>

<style scoped>
    .app-container {
        background: #f5f5f5;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 100%;
        min-width: 637px;
        margin-bottom: 20px;
    }

    .survey-container {

    }

    .survey-item-wrap {
        padding: 5px 0;
    }

    .survey-item {
        border-right: 1px solid #e0e0e0;
    }

    .survey-item-wrap:last-child .survey-item {
        border: none;
    }

    .survey-item h3 {
        font-size: 13px;
        color: #666;
    }

    .survey-item p {
        color: #f88b00;
        font-size: 25px;
    }

    .content-rowspan div {
        padding-left: 13px;
        line-height: 46px;
        border-bottom: 1px solid #ECEDEE;
    }

    .extend .cell {
        padding: 0;
    }

    .flow-details {
        border-bottom: 1px solid #e8e8e8;
    }

    .flow-details:last-child {
        border: none;
    }
</style>



